.pop-up-oops-container {
  width: 100%;
  height: 100%;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  display: none;
  position: fixed;
  top: 0;
}

.pop-up-oops {
  width: 700px;
  height: 400px;
  background-color: #111;
  border-radius: 20px;
  display: flex;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, .42);
}

.pop-up-oops-description {
  text-align: left;
  flex-direction: column;
  justify-content: center;
  margin-left: 50px;
  font-family: Roboto;
  font-size: 24px;
  font-weight: 400;
  line-height: 28.13px;
  display: flex;
  position: absolute;
  top: 150px;
}

.pop-up-oops-image {
  width: 363px;
  height: 318px;
  justify-content: center;
  align-items: center;
  margin: 60px 0 0 60px;
  display: flex;
  position: absolute;
  bottom: 5px;
  right: 0;
}

.closePopUpOops {
  width: 24px;
  height: 24px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 40px;
  right: 40px;
}

